<template>
  <div>
    <div class="container">
      <div class="rightNav">
        <h3>Datablau 数据资源管理</h3>
        <form action="" id="login">
          <i class="logo user"></i><input type="text" class="username">
          <i class="logo lock"></i><input type="password" class="password">
          <a href="javascript:;" id="submit" @click="go">登 录</a>
        </form>
        <div class="keep">
          <input id="save" type="checkbox"><label for="save"> 保持登录状态</label>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  methods:{
    go(){
      this.$router.push({name: 'dataPreview',params:{username:'tom'}});
    }
  }
}
</script>
<style scoped lang="scss">
  input:-webkit-autofill {
    -webkit-box-shadow:0 0 0px 1000px rgba(255,255,255,0.9) inset ;
    border:0px 0px 1px 0px solid #CCC !important ;
  }
  .container {
    position: absolute;
    width: 100%;
    height: 100%;
    background: url('../../assets/images/bg.jpg') no-repeat;
    background-size: cover;
  }
  .rightNav {
    position: absolute;
    right: 0;
    width: 600px;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.9);
  }
  .container .rightNav h3 {
    margin-top: 250px;
    margin-left: 180px;
    font-size: 25px;
    font-weight: normal;
  }
  #login {
    position: absolute;
    width: 500px;
    height: 500px;
    left: 72px;
    bottom: 100px;
  }
  #login input{
    width: 316px;
    border-bottom: 1px solid #DADADA;
    height: 41px;
    font-size: 20px;
    text-indent: 10px;
    margin-left: 113px;
    padding-top: 5px;
  }
  #login .username {
    margin-top: 80px;

  }
  #login .password {
    margin-top: 35px;
  }
  .logo {
    position: absolute;
    width: 35px;
    height: 35px;
  }
  .user {
    background: url('../../assets/images/user.png') no-repeat;
    background-size: cover;
    left: 75px;
    top: 85px;
  }
  .lock {
    background: url('../../assets/images/lock.png') no-repeat;
    background-size: cover;
    left: 75px;
    top: 165px;
  }
  #submit {
    display: block;
    position: absolute;
    padding-top: 6px;
    left: 100px;
    top: 285px;
    width: 300px;
    height: 40px;
    background-color: #22a3e6;
    border-radius: 20px;
    text-align: center;
    cursor: pointer;
    border: none;
    color: #FFFFFF;
    font-size: 18px;
    opacity: 0.8;
    outline: none;
  }
  #submit:hover {
   background-color: lighten(#22a3e6,5%);
 }
 .container .keep {
  position: absolute;
  color: #C3C3C3;
  font-size: 16px;
  vertical-align: middle;
  left: 255px;
  bottom: 200px;
}
</style>



